<template>
  <div class="cms-list-layout">
    <cms-breadcrumb to="/memberGrade/list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <cms-prompt collapse='true'>
      <li>显示所有会员等级的信息。</li>
      <li>可以添加会员等级、编辑会员等级、删除会员等级。</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>会员账户</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>邮箱</label>
        <el-input v-model="queryParams.email" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>手机</label>
        <el-input v-model="queryParams.phone" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe ref='dataTable'>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="用户名" min-width="180"></el-table-column>
      <el-table-column prop="email" label="邮箱" min-width="180"></el-table-column>
      <el-table-column prop="phone" label="手机" min-width="140"></el-table-column>
      <el-table-column prop="balance" label="余额/积分" min-width="140">
        <template slot-scope="scope">
          <span v-if="scope.row.memberAccount">{{scope.row.memberAccount.balance}}/{{scope.row.memberAccount.score}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="levelName" label="成长值/会员等级" min-width="140">
        <template slot-scope="scope">
          <span v-if="scope.row.memberAccount&&scope.row.memberLevel">
            {{scope.row.memberAccount.exp}}/{{scope.row.memberLevel.levelName}}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="注册日期" min-width="160">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import { memberList } from '@/api/users/user.js'
import list from '@/mixins/list'
var defaultData = {
  email: '',
  phone: '',
  enabled: '',
  level: '',
  username: '',
  startTime: '',
  endTime: '',
  size: 10,
  page: 1,
  isOpenStore: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {

  },
  filters: {
    codeFiller(val) {
      switch (val) {
        case '1':
          return '购物福利 '
        case '2':
          return '生日特权 '
        default:
      }
    }
  },
  created() {
    this.queryParams.level = this.$route.query.id
    this.getTableData(memberList, this.queryParams)
  }
}
</script>

<style>
</style>



